.about {
  display: flex;
  align-items: center; /* Pusatkan elemen secara vertikal */
  justify-content: space-between; /* Buat jarak maksimal antara teks & foto */
  gap: 50px; /* Beri jarak antara teks dan foto */
  height: 50vh; /* Pastikan section memenuhi tinggi layar */
  text-align: left; /* Pastikan teks tetap rata kiri */
}

.about .name {
  flex: 1; /* Biar teks memenuhi ruang yang tersedia */
  text-align: left; /* Pastikan teks rata kiri */
}

.text-container {
  flex: 1; /* Agar teks memenuhi ruang yang tersedia */
  text-align: left; /* Pastikan teks rata kiri */
  padding-left: 80px;
}

.name {
  color: rgb(0, 0, 0);
  display: flex; /* Enable flexbox */
  flex-direction: column; /* Stack children vertically */
  align-items: flex-start; /* Pindahkan ke kiri */
  text-align: left; /* Pastikan teks juga rata kiri */
  position: relative;
  margin-top: 30px;
  margin-bottom: 0px;
  padding-left: 40px; /* Beri padding agar tidak terlalu mepet */
}

.new-line {
  display: block; /* Memastikan teks turun ke baris baru */
  font-size: 50px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.new-line-2 {
  display: block; /* Memastikan teks turun ke baris baru */
  font-size: 50px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.new-paragraph {
  display: block;
}

/* Container untuk link agar berdampingan */
.link-container {
  display: flex;
  padding: 0 40px;
  gap: 10px; /* Jarak antara kedua link */
  margin-top: 30px; /* Tambahan jarak atas */
}

/* CSS untuk link-pdf */
.link-pdf {
  background-color: red;
  color: rgb(255, 255, 255);
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/* CSS saat link-pdf di-hover */
.link-pdf:hover {
  background-color: rgb(236, 139, 139);
  color: rgb(255, 255, 255);
}

/* CSS untuk link-telegram */
.link-telegram {
  display: inline-flex; /* Gunakan flexbox untuk elemen inline */
  align-items: center; /* Center secara vertikal */
  background-color: #007bff;
  color:  #ffffff;
  padding: 10px 15px;
  text-decoration: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/* CSS saat link-telegram di-hover */
.link-telegram:hover {
  background-color: rgb(176, 120, 250);
  color: rgb(255, 255, 255);
}
